import * as tslib_1 from "tslib";
import { Component, Vue } from "vue-property-decorator";
import { GetDailyStudiesAsync } from "@/api/report";
import { GetAllCourseGroups } from "@/api/course-group";
import moment from 'moment';
let CeteleGenelToplam = class CeteleGenelToplam extends Vue {
    constructor() {
        super(...arguments);
        this.isLoading = true;
        this.veri = [];
        this.courses = [];
        this.courseGroupId = '';
        this.startDate = '';
        this.endDate = '';
        this.dateValue = [];
        this.validated = false;
        this.timeTyme = [
            { displayName: 'Haftalık', key: 'week' },
            { displayName: 'Aylık', key: 'month' },
        ];
        this.sortables = [
            { prop: 'kuran', order: 'descending' },
        ];
        this.pickerOptions = {
            shortcuts: [{
                    text: 'Geçen Hafta',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                        picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '2 Hafta Önce',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 14);
                        picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: '3 Hafta Önce',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 21);
                        picker.$emit('pick', [start, end]);
                    }
                },
                {
                    text: 'Geçen Ay',
                    onClick(picker) {
                        const end = new Date();
                        const start = new Date();
                        start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                        picker.$emit('pick', [start, end]);
                    }
                }
            ],
        };
    }
    get IsRaporOlusturDisable() {
        return this.veri.length === 0;
    }
    created() {
        this.GetAllCourseGroups();
        //this.GetDailyStudies();
    }
    exportExcel() {
        import('./Export2Excel.js').then(excel => {
            //const headers = ['No'];
            const headers = [];
            const data = [];
            //let count = 1;
            let iIndex = 0;
            for (let i in this.veri) {
                //let key = i;
                const val = this.veri[i];
                const arr = [];
                //arr.push(count);
                //count++;
                for (let j in val) {
                    const sub_key = j;
                    const sub_val = val[j];
                    if (iIndex == 0) {
                        headers.push(sub_key);
                    }
                    arr.push(sub_val);
                }
                iIndex++;
                data.push(arr);
            }
            excel.export_json_to_excel({
                header: headers,
                data,
                filename: 'çetele-liste',
                autoWidth: true,
                bookType: 'xlsx' //Optional
            });
        });
    }
